import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import { ImageViewerController } from 'ionic-img-viewer';
import { Observable } from 'rxjs';
import { Gallery, Collections, Note } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage({ segment: 'galeria/:id' })
@Component({
  selector: 'page-galeria-detail',
  templateUrl: 'galeria-detail.html',
})
export class GaleriaDetailPage {

  private id: string = this.navParams.get('id')
  public uid: string = localStorage.getItem('uid')

  public newNota: any = {text: '', title: ''}
  public lc: boolean = false
  public gallery$: Observable<Gallery>
  public notas$: Observable<Note[]>;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private toast: ToastController,
    private imgCtrl: ImageViewerController,
    private afs: AngularFirestore,
    private load: LoadingController,
    private auth: AuthProvider,
  ) {
  }

  ionViewDidLoad() {

    this.gallery$ = this.afs.collection(Collections.GALLERY).doc<Gallery>(this.id).valueChanges()

    this.auth.user$.subscribe(user => {
      if (user && !this.notas$) {
        this.notas$ = this.afs.collection<Note>(Collections.NOTE, ref => ref
          .where('parent_id', '==', this.id)
          .where('user', '==', user.uid))
          .valueChanges();
      }
    })

  }

  openImg(img) {
    this.imgCtrl.create(img).present()
  }

  async addNota(title: string, text: string) {

    if (!title) return this.toast.create({ message: 'Ingresa un titulo', duration: 2000 }).present()
    if (!text) return this.toast.create({ message: 'Ingresa un text', duration: 2000 }).present()

    const l = this.load.create({ content: 'Agregando...' })
    l.present()

    const id = this.afs.createId();
    await this.afs.collection(Collections.NOTE).doc(id).set({
      id,
      title,
      text,
      parent_id: this.id,
      user: this.auth.user.uid,
    });

    l.dismiss();

    /* this.back.addNota(title, text, this.type, this.id).subscribe(data => {}, err => {
      this.toast.create({message: 'No se pudo agregar tu nota...', duration: 2000}).present()
      l.dismiss()
    }) */

  }

}



